import React from 'react'
import { storiesOf } from '@storybook/react'
import { MyPopup } from '../components/MyPopup';
const stories = storiesOf('MyPopup', module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } });

stories.add('Success', () => {
    const [isPopupOpen, TogglePopup] = useCycle(false, true);

    return(
        <>
            <button
                onClick={TogglePopup}>
                Trigger
            </button>
            <MyPopup 
                text="Bravo la popup est fonctionnelle"
                type="success"
                title="Success"
                trigger={isPopupOpen}
                onClose={TogglePopup}
            />
        </>
    )
});
stories.add('Error', () => {
    const [isPopupOpen, TogglePopup] = useCycle(false, true);

    return(
        <>
            <button
                onClick={TogglePopup}>
                Trigger
            </button>
            <MyPopup 
                text="une erreur est survenue"
                type="error"
                title="Error"
                trigger={isPopupOpen}
                onClose={TogglePopup}
            />
        </>
    )
})